import React, { useState, useEffect, useCallback } from 'react';
import { 
  Grid, 
  TextField, 
  Typography, 
  Button, 
  Box,
  InputAdornment,
  Paper,
  Tooltip,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  SelectChangeEvent
} from '@mui/material';
import RefreshIcon from '@mui/icons-material/Refresh';
import EmailIcon from '@mui/icons-material/Email';
import { SocialMediaData, ContactInfo, UserData } from '../types/index';
import { socialPlatforms, getPaginatedPlatforms } from '../assets/socialPlatforms';
import { useThemeContext } from '../contexts/ThemeContext';

interface MediaFormProps {
  userData: UserData;
  onUpdateUserData: (data: UserData) => void;
}

const initialSocialMediaData: SocialMediaData = {
  wechat: null,
  xiaohongshu: null,
  bilibili: null,
  douyin: null,
  videoAccount: null,
  juejin: null,
  zhihu: null,
  twitter: null,
  kuaishou: null,
  weibo: null
};

const initialContactInfo: ContactInfo = {
  wechatId: '',
  email: ''
};

const MediaForm: React.FC<MediaFormProps> = React.memo(({ userData, onUpdateUserData }) => {
  const { mode, t } = useThemeContext();
  const isDarkMode = mode === 'dark';
  
  const [socialMedia, setSocialMedia] = useState<SocialMediaData>(
    userData.socialMedia || initialSocialMediaData
  );
  const [contactInfo, setContactInfo] = useState<ContactInfo>(
    userData.contactInfo || initialContactInfo
  );
  const [contactType, setContactType] = useState<string>('wechat');
  const [localWechatId, setLocalWechatId] = useState(userData.contactInfo?.wechatId || '');
  const [localEmail, setLocalEmail] = useState(userData.contactInfo?.email || '');
  const [localSocialMedia, setLocalSocialMedia] = useState<{[key: string]: string}>({});
  const [visiblePlatformsCount, setVisiblePlatformsCount] = useState<number>(9);

  // 当父组件传入的userData变化时更新组件状态
  useEffect(() => {
    if (userData) {
      setSocialMedia(userData.socialMedia || initialSocialMediaData);
      setContactInfo(userData.contactInfo || initialContactInfo);
      setLocalWechatId(userData.contactInfo?.wechatId || '');
      setLocalEmail(userData.contactInfo?.email || '');
      
      // 初始化本地输入值
      const initialLocalValues: {[key: string]: string} = {};
      Object.entries(userData.socialMedia || initialSocialMediaData).forEach(([key, value]) => {
        initialLocalValues[key] = value === null ? '' : value.toString();
      });
      setLocalSocialMedia(initialLocalValues);
    }
  }, [userData]);

  // 优化更新函数，使用useCallback包装
  const updateUserData = useCallback(() => {
    onUpdateUserData({
      socialMedia,
      contactInfo,
      updatedAt: new Date()
    });
  }, [socialMedia, contactInfo, onUpdateUserData]);

  // 使用节流更新父组件
  useEffect(() => {
    const timer = setTimeout(() => {
      updateUserData();
    }, 500); // 500ms节流

    return () => clearTimeout(timer);
  }, [socialMedia, contactInfo, updateUserData]);

  const handleSocialMediaChange = (platform: string, value: string) => {
    // 更新本地状态，保持输入框稳定
    setLocalSocialMedia((prev: {[key: string]: string}) => ({
      ...prev,
      [platform]: value
    }));
    
    // 延迟更新实际数据，避免界面频繁重绘导致闪烁
    const numValue = value === '' ? null : Number(value);
    setSocialMedia((prev: SocialMediaData) => ({
      ...prev,
      [platform]: numValue
    }));
  };

  const handleContactInfoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (contactType === 'wechat') {
      setLocalWechatId(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        wechatId: e.target.value
      }));
    } else if (contactType === 'email') {
      setLocalEmail(e.target.value);
      setContactInfo(prev => ({
        ...prev,
        email: e.target.value
      }));
    }
  };

  const handleContactTypeChange = (e: SelectChangeEvent) => {
    setContactType(e.target.value);
  };

  const handleReset = () => {
    setSocialMedia(initialSocialMediaData);
    setContactInfo(initialContactInfo);
  };

  const handleShowMorePlatforms = () => {
    setVisiblePlatformsCount(prev => prev + 6);
  };

  // 获取当前应该显示的平台
  const visiblePlatforms = getPaginatedPlatforms(visiblePlatformsCount);
  // 是否还有更多平台可以显示
  const hasMorePlatforms = visiblePlatformsCount < socialPlatforms.length;

  return (
    <Paper 
      elevation={3} 
      sx={{ 
        p: 3, 
        borderRadius: '12px',
        backgroundColor: isDarkMode ? 'rgba(255,255,255,0.05)' : '#ffffff',
        border: '1px solid',
        borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.08)',
        boxShadow: isDarkMode 
          ? '0 8px 20px rgba(0,0,0,0.2)' 
          : '0 10px 25px rgba(0,0,0,0.08)',
        transition: 'all 0.3s ease'
      }}
    >
      <Typography 
        variant="h5" 
        gutterBottom 
        sx={{ 
          fontWeight: 600, 
          color: isDarkMode ? '#fff' : '#1a1a2e',
          borderBottom: '2px solid',
          borderColor: 'primary.main',
          pb: 1,
          mb: 3
        }}
      >
        {t('inputYourData')}
      </Typography>
      
      <Grid container spacing={3}>
        {visiblePlatforms.map(platform => (
          <Grid item xs={12} sm={6} md={4} key={platform.id}>
            <TextField
              fullWidth
              label={platform.name}
              type="number"
              placeholder={t('fansPlaceholder')}
              value={localSocialMedia[platform.id] !== undefined ? localSocialMedia[platform.id] : (socialMedia[platform.id] === null ? '' : socialMedia[platform.id])}
              onChange={(e) => handleSocialMediaChange(platform.id, e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Box 
                      component="img"
                      sx={{ 
                        width: 20, 
                        height: 20,
                        mr: 1,
                        objectFit: 'contain',
                        transition: 'transform 0.3s ease',
                        '&:hover': {
                          transform: 'scale(1.15)'
                        }
                      }}
                      alt={platform.name}
                      src={platform.pngPath}
                      onError={(e: React.SyntheticEvent<HTMLImageElement, Event>) => {
                        const target = e.target as HTMLImageElement;
                        target.onerror = null;
                        target.src = platform.iconPath;
                      }}
                    />
                  </InputAdornment>
                ),
                endAdornment: <InputAdornment position="end">{t('followers')}</InputAdornment>,
                sx: {
                  borderRadius: '10px',
                  transition: 'all 0.3s ease',
                  '& .MuiOutlinedInput-notchedOutline': {
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.2)',
                    transition: 'border-color 0.3s ease'
                  },
                  '&:hover .MuiOutlinedInput-notchedOutline': {
                    borderColor: isDarkMode ? 'rgba(255,255,255,0.3)' : 'rgba(0,0,0,0.3)',
                    boxShadow: '0 0 8px rgba(0,0,0,0.05)'
                  },
                  '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                    borderColor: platform.color,
                    boxShadow: `0 0 0 2px ${platform.color}25`
                  },
                  '&:hover': {
                    transform: 'translateY(-2px)'
                  }
                }
              }}
              inputProps={{
                min: 0,
                max: 999999999,
              }}
              sx={{
                '& .MuiInputLabel-root': {
                  color: isDarkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)'
                },
                '& .MuiInputLabel-root.Mui-focused': {
                  color: platform.color
                }
              }}
            />
          </Grid>
        ))}
      </Grid>
      
      {hasMorePlatforms && (
        <Box 
          sx={{ 
            display: 'flex', 
            justifyContent: 'center', 
            mt: 4,
            mb: 2 
          }}
        >
          <Button
            variant="outlined"
            color="primary"
            onClick={handleShowMorePlatforms}
            startIcon={<Box sx={{ display: 'flex', alignItems: 'center' }}>+</Box>}
            sx={{
              borderRadius: '20px',
              px: 3,
              py: 1,
              borderWidth: '2px',
              fontWeight: 500,
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-2px)',
                boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
              }
            }}
          >
            {t('morePlatforms')} ({socialPlatforms.length - visiblePlatformsCount}{t('remaining')})
          </Button>
        </Box>
      )}
      
      <Box sx={{ mt: 6, mb: 3 }}>
        <Typography 
          variant="h6" 
          gutterBottom 
          sx={{ 
            fontWeight: 600, 
            color: isDarkMode ? '#fff' : '#1a1a2e',
            borderBottom: '1px solid',
            borderColor: isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)',
            pb: 1,
            mb: 3
          }}
        >
          {t('contactWay')}
        </Typography>
        
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12}>
            <Box sx={{ 
              display: 'flex', 
              flexDirection: 'column',
              gap: 2,
              position: 'relative',
              transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)'
            }}>
              <FormControl sx={{ 
                position: 'relative',
                boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                borderRadius: '12px',
                border: '1px solid',
                borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                p: 2,
                transform: 'translateY(0)',
                transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: '0 8px 16px rgba(0,0,0,0.12)'
                }
              }}>
                <InputLabel id="contact-type-label" sx={{ 
                  position: 'relative', 
                  transform: 'none',
                  color: isDarkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)',
                  fontSize: '0.9rem',
                  mb: 1,
                  fontWeight: 500
                }}>{t('contactInfoType')}</InputLabel>
                <Select
                  labelId="contact-type-label"
                  value={contactType}
                  onChange={handleContactTypeChange}
                  sx={{
                    '& .MuiOutlinedInput-notchedOutline': { border: 'none' },
                    '&:hover .MuiOutlinedInput-notchedOutline': { border: 'none' },
                    '&.Mui-focused .MuiOutlinedInput-notchedOutline': { border: 'none' },
                    bgcolor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'white',
                    borderRadius: '8px',
                    boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
                    fontWeight: 500
                  }}
                >
                  <MenuItem value="wechat">{t('wechatId')}</MenuItem>
                  <MenuItem value="email">{t('email')}</MenuItem>
                </Select>
              </FormControl>
              
              <Box sx={{
                position: 'relative',
                borderRadius: '12px',
                boxShadow: '0 4px 12px rgba(0,0,0,0.08)',
                border: '1px solid',
                borderColor: isDarkMode ? 'rgba(255,255,255,0.15)' : 'rgba(0,0,0,0.08)',
                bgcolor: isDarkMode ? 'rgba(255,255,255,0.05)' : 'rgba(255,255,255,0.8)',
                p: 2,
                mt: 1,
                transform: 'translateY(0)',
                transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: '0 8px 16px rgba(0,0,0,0.12)'
                }
              }}>
                <TextField
                  fullWidth
                  label={contactType === 'wechat' ? t('wechatId') : t('email')}
                  placeholder={contactType === 'wechat' 
                    ? t('wechatPlaceholder')
                    : t('emailPlaceholder')}
                  value={contactType === 'wechat' ? localWechatId : localEmail}
                  onChange={handleContactInfoChange}
                  variant="standard"
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        {contactType === 'email' ? 
                          <EmailIcon sx={{ color: isDarkMode ? 'rgba(255,255,255,0.7)' : '#1976d2' }} /> : 
                          <Box 
                            component="img"
                            sx={{ 
                              width: 24, 
                              height: 24,
                              mr: 1
                            }}
                            alt="微信"
                            src={`${process.env.PUBLIC_URL}/icons/wechat.png`}
                            onError={(e: React.SyntheticEvent<HTMLImageElement, Event>) => {
                              const target = e.target as HTMLImageElement;
                              target.onerror = null;
                              target.src = `${process.env.PUBLIC_URL}/icons/wechat.svg`;
                            }}
                          />
                        }
                      </InputAdornment>
                    ),
                    disableUnderline: true,
                    sx: {
                      px: 2,
                      py: 1,
                      bgcolor: isDarkMode ? 'rgba(255,255,255,0.08)' : 'white',
                      borderRadius: '8px',
                      boxShadow: '0 2px 8px rgba(0,0,0,0.05)',
                      '&:hover': {
                        bgcolor: isDarkMode ? 'rgba(255,255,255,0.12)' : 'rgba(242,242,242,1)'
                      },
                      transition: 'background-color 0.3s ease'
                    }
                  }}
                  sx={{ 
                    '& .MuiFormLabel-root': {
                      color: isDarkMode ? 'rgba(255,255,255,0.7)' : 'rgba(0,0,0,0.7)',
                      fontSize: '0.9rem',
                      transform: 'translate(0, -12px) scale(1)',
                      fontWeight: 500
                    },
                    '& .MuiInputLabel-shrink': {
                      transform: 'translate(0, -12px) scale(1)'
                    }
                  }}
                />
              </Box>
            </Box>
          </Grid>
        </Grid>
        
        <Tooltip title={t('resetAll')}>
          <Button 
            variant="outlined" 
            color="error" 
            onClick={handleReset}
            startIcon={<RefreshIcon />}
            sx={{ 
              minWidth: '100px',
              height: '46px',
              borderRadius: '8px',
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-2px)',
                boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
              }
            }}
          >
            {t('resetData')}
          </Button>
        </Tooltip>
      </Box>
    </Paper>
  );
});

export default MediaForm; 