import React, { createContext, useContext, useState, useEffect, useMemo } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import zhLocale from '../locales/zh';
import enLocale from '../locales/en';

type ThemeMode = 'light' | 'dark';
type Language = 'zh' | 'en';

interface ThemeContextType {
  mode: ThemeMode;
  toggleMode: () => void;
  language: Language;
  toggleLanguage: () => void;
  t: (key: string) => string;
}

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

export const useThemeContext = () => {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useThemeContext must be used within a ThemeContextProvider');
  }
  return context;
};

interface ThemeContextProviderProps {
  children: React.ReactNode;
}

export const ThemeContextProvider: React.FC<ThemeContextProviderProps> = ({ children }) => {
  // 检测浏览器首选语言
  const detectBrowserLanguage = (): Language => {
    const storedLanguage = localStorage.getItem('language');
    if (storedLanguage === 'zh' || storedLanguage === 'en') {
      return storedLanguage;
    }
    
    const browserLang = navigator.language.toLowerCase();
    return browserLang.startsWith('zh') ? 'zh' : 'en';
  };

  // 检测首选主题模式
  const detectPreferredMode = (): ThemeMode => {
    const storedMode = localStorage.getItem('theme');
    if (storedMode === 'light' || storedMode === 'dark') {
      return storedMode;
    }
    
    return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
  };

  const [mode, setMode] = useState<ThemeMode>('light');
  const [language, setLanguage] = useState<Language>('zh');

  // 初始化主题和语言设置
  useEffect(() => {
    setMode(detectPreferredMode());
    setLanguage(detectBrowserLanguage());
  }, []);

  // 保存主题偏好到本地存储
  useEffect(() => {
    localStorage.setItem('theme', mode);
    document.documentElement.setAttribute('data-theme', mode);
  }, [mode]);

  // 保存语言偏好到本地存储
  useEffect(() => {
    localStorage.setItem('language', language);
    document.documentElement.setAttribute('lang', language);
  }, [language]);

  // 翻译函数
  const t = (key: string): string => {
    const translations = language === 'zh' ? zhLocale : enLocale;
    return translations[key] || key;
  };

  const toggleMode = () => {
    setMode(prevMode => (prevMode === 'light' ? 'dark' : 'light'));
  };

  const toggleLanguage = () => {
    setLanguage(prevLang => (prevLang === 'zh' ? 'en' : 'zh'));
  };

  // 创建主题
  const theme = useMemo(() => {
    return createTheme({
      palette: {
        mode,
        primary: {
          main: '#1976d2',
        },
        secondary: {
          main: '#9c27b0',
        },
        background: {
          default: mode === 'light' ? '#f5f5f5' : '#121212',
          paper: mode === 'light' ? '#ffffff' : '#1e1e1e',
        },
        text: {
          primary: mode === 'light' ? '#212121' : '#e0e0e0',
          secondary: mode === 'light' ? '#757575' : '#9e9e9e',
        },
      },
      typography: {
        fontFamily: [
          '-apple-system',
          'BlinkMacSystemFont',
          '"Segoe UI"',
          'Roboto',
          '"Helvetica Neue"',
          'Arial',
          'sans-serif',
          '"Apple Color Emoji"',
          '"Segoe UI Emoji"',
          '"Segoe UI Symbol"',
        ].join(','),
      },
      shape: {
        borderRadius: 8,
      },
      components: {
        MuiButton: {
          styleOverrides: {
            root: {
              textTransform: 'none',
              borderRadius: 8,
              fontWeight: 600,
            },
          },
        },
        MuiPaper: {
          styleOverrides: {
            root: {
              backgroundImage: 'none',
            },
          },
        },
      },
    });
  }, [mode]);

  const contextValue = {
    mode,
    toggleMode,
    language,
    toggleLanguage,
    t,
  };

  return (
    <ThemeContext.Provider value={contextValue}>
      <ThemeProvider theme={theme}>
        {children}
      </ThemeProvider>
    </ThemeContext.Provider>
  );
}; 